import { list, h } from 'forest';
import { NodeList, Node, NodeTitle, NodeContent, Content } from './components';
export function Effects($effects) {
    NodeList(() => {
        const $list = $effects.map((map) => Object.entries(map).map(([sid, meta]) => ({ sid, ...meta })));
        list({
            source: $list,
            key: 'sid',
            fields: ['name', 'inFlight'],
            fn({ fields: [$name, $inFlight] }) {
                Node(() => {
                    NodeTitle({ text: [$name, ' '] });
                    NodeContent(() => {
                        h('span', () => {
                            h('span', { text: [' {'] });
                            Content.string({ text: `"inFlight": ` });
                            Content.number({ text: $inFlight });
                            h('span', { text: ['}'] });
                        });
                    });
                });
            },
        });
    });
}
