import { createStore, createEvent, combine, restore } from 'effector';
import { list, h } from 'forest';
import { NodeList, Node, NodeTitle, NodeContent, Panel, Checkbox, Input, NodeButton, } from './components';
import { ObjectView } from './object-view';
import { createJsonSetting, createSetting } from './setting';
const kindSetting = createJsonSetting('filter-kinds');
const textSetting = createSetting('filter-text');
export function Logs($logs) {
    const defaultKinds = ['event', 'store'];
    const toggleKind = createEvent();
    const filterChanged = createEvent();
    const clearClicked = createEvent();
    const $kinds = createStore(kindSetting.read(defaultKinds));
    const $filterText = restore(filterChanged, textSetting.read(''));
    $kinds
        .on(toggleKind, (exist, toggled) => exist.includes(toggled)
        ? exist.filter((i) => i !== toggled)
        : [...exist, toggled])
        .watch(kindSetting.write);
    $filterText.watch(textSetting.write);
    $logs.on(clearClicked, () => []);
    Panel(() => {
        h('span', { text: 'Show: ' });
        Checkbox({
            title: 'Event',
            attr: { checked: $kinds.map((list) => list.includes('event')) },
            handler: { click: toggleKind.prepend(() => 'event') },
        });
        Checkbox({
            title: 'Store',
            attr: { checked: $kinds.map((list) => list.includes('store')) },
            handler: { click: toggleKind.prepend(() => 'store') },
        });
        Checkbox({
            title: 'Effect',
            attr: { checked: $kinds.map((list) => list.includes('effect')) },
            handler: { click: toggleKind.prepend(() => 'effect') },
        });
        h('span', { text: 'Filter:' });
        Input({
            attr: { value: $filterText },
            handler: {
                change: filterChanged.prepend((event) => event.currentTarget?.value ?? ''),
            },
        });
        NodeButton({ text: 'Clear', handler: { click: clearClicked } });
    });
    NodeList(() => {
        list({
            source: $logs,
            key: 'id',
            fields: ['kind', 'name', 'payload', 'datetime'],
            fn({ fields: [$kind, $name, $payload, $datetime] }) {
                const $kindMatched = combine($kind, $kinds, (current, visible) => visible.includes(current));
                const $textMatched = combine($filterText, $name, (filter, name) => name.includes(filter));
                const $visible = combine($kindMatched, $textMatched, (kind, text) => kind && text);
                Node({
                    visible: $visible,
                    fn() {
                        const $iso = $datetime.map((date) => date.toISOString());
                        const $time = $datetime.map((date) => date.toLocaleTimeString());
                        NodeTitle({ text: [$time, ' â–¸ '], attr: { title: $iso } });
                        NodeTitle({ text: $kind });
                        NodeTitle({ text: [' Â«', $name, 'Â» '] });
                        NodeContent(() => ObjectView({ value: $payload }));
                    },
                });
            },
        });
    });
}
